import React, { Component } from 'react';
import { TableDataEditCell, InputMoney } from 'components/common';
import { moneyToString } from 'utils/formatUtil';
import { Form } from 'antd';

export default class TableSubjectData extends Component {
  state = {
    reload: 0,
  };

  columns = [
    {
      title: 'K hiu',
      dataIndex: 'kyhieu',
      key: 'kyhieu',
    },
    {
      title: 'Tn mn',
      dataIndex: 'tenmon',
      key: 'tenmon',
    },
    {
      title: 'S tn ch',
      dataIndex: 'sotinchi',
      key: 'sotinchi',
    },
    {
      title: 'Tn lp hc li',
      dataIndex: 'tenlophoclai',
      key: 'tenlophoclai',
    },
    {
      title: 'S SV',
      dataIndex: 'sosinhvien',
      key: 'sosinhvien',
    },
    {
      title: 'n gi',
      dataIndex: 'dongia',
      key: 'dongia',
      editable: true,
      render: text => moneyToString(text),
      renderFormItemEdit: ({ form, record, setRefFormItem, onSave }) => (
        <Form.Item>
          {form.getFieldDecorator(`${record.index}.dongia`, {
            initialValue: record.dongia,
          })(
            <InputMoney
              onPressEnter={onSave}
              onBlur={onSave}
              refInput={setRefFormItem}
              style={{ minWidth: '200px' }}
            />
          )}
        </Form.Item>
      ),
    },
    {
      title: 'Thnh tin',
      dataIndex: 'thanhtien',
      key: 'thanhtien',
      render: (text, record) => moneyToString(record.dongia * record.sosinhvien),
    },
  ];

  handleSaveCellData = newSubject => {
    this.props.handleUpdateDonGia(newSubject);
    return newSubject;
  };

  render() {
    const { service, rowSelection, paramSearch } = this.props;
    return (
      <TableDataEditCell
        rowSelection={rowSelection}
        reload={this.state.reload}
        paramSearch={paramSearch}
        columns={this.columns}
        service={service}
        handleSaveCellData={this.handleSaveCellData}
        rowKey="index"
      />
    );
  }
}



