import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Btn, BtnAddNew, BtnDelete, BtnExport } from 'components/common/button';
import { getReTuitionReceipts, getStudents } from 'services/reTuitionReceipts';
import { getQueryParams } from 'utils/urlUtil';
import { Card, Row, Icon } from 'antd';
import TableSubjectData from './TableSubjectData';
import TableStudentData from './TableStudentData';
import SearchBar from './SearchBar';
// import './style.css';

class ReTuitionReceipts extends Component {
  state = {
    paramSearch: {},
    subjectSelected: {},
    studentSelected: {},
    students: [],
    studentSelectedRowKeys: [],
    masterPrice: '',
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onChangeMasterPrice = value => {
    this.setState({
      masterPrice: value,
    });
  };

  getStudentsBySubject = index =>
    getStudents(index).map(student => this.updateStudentKey(student, index));

  addStudents = () => {
    const addedStudentKeys = this.state.students.map(student => student.key);
    const newStudents = Object.values(this.state.subjectSelected).flatMap(subject =>
      this.getStudentsBySubject(subject.index.toString())
        .filter(student => !addedStudentKeys.includes(student.key))
        .map(student => this.updateStudentInfo(student, subject))
    );
    this.setState({
      students: [...this.state.students, ...newStudents],
    });
  };

  removeStudents = () => {
    const removeStudentKeys = Object.values(this.state.studentSelected).map(student => student.key);
    const students = this.state.students.filter(
      student => !removeStudentKeys.includes(student.key)
    );
    this.setState({
      students,
      studentSelected: [],
      studentSelectedRowKeys: [],
    });
  };

  updateStudentInfo = (student, subject) => {
    student.sotienphainop = this.state.masterPrice || subject.dongia;
    let sotienconnop = student.sotienphainop - student.sotienmiengiam;
    if (sotienconnop < 0) sotienconnop = 0;
    student.sotienconnop = sotienconnop;
    this.updateStudentKey(student, subject.index);
    return student;
  };

  updateStudentKey = (student, subjectIndex) => {
    student.key = `${student.masinhvien} - ${subjectIndex}`;
    return student;
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleUpdateDonGia = newSubject => {
    this.setState({
      subjectSelected: { ...this.state.subjectSelected, [newSubject.index]: newSubject },
    });
  };

  enableAddBtn = () =>
    this.state.subjectSelected && Object.values(this.state.subjectSelected).length > 0;
  enableDeleteBtn = () =>
    this.state.studentSelected && Object.values(this.state.studentSelected).length > 0;
  enablePrintAndExport = () => this.state.students.length > 0;

  render() {
    const { studentSelectedRowKeys } = this.state;
    const studentSelection = {
      selectedRowKeys: studentSelectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const studentSelected = {};

        selectedRows.forEach(selectedRow => {
          studentSelected[selectedRow.key] = selectedRow;
        });

        this.setState({
          studentSelected,
          studentSelectedRowKeys: selectedRowKeys,
        });
      },
    };

    const subjectSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const subjectSelected = {};

        selectedRows.forEach(selectedRow => {
          subjectSelected[selectedRow.index] = selectedRow;
        });

        this.setState({
          subjectSelected,
        });
      },
    };

    return (
      <Card>
        <Row>
          <SearchBar
            masterPrice={this.state.masterPrice}
            onChangeMasterPrice={this.onChangeMasterPrice}
          />
        </Row>
        <Row>
          <TableSubjectData
            service={getReTuitionReceipts}
            rowSelection={subjectSelection}
            paramSearch={this.state.paramSearch}
            handleUpdateDonGia={this.handleUpdateDonGia}
          />
        </Row>
        <Row id="btns-action">
          <BtnAddNew
            title="Thm SV khon thu"
            onClick={this.addStudents}
            disabled={!this.enableAddBtn()}
          />
          <BtnDelete
            title="Xa SV khon thu"
            onClick={this.removeStudents}
            disabled={!this.enableDeleteBtn()}
          />
          <Btn disabled={!this.enablePrintAndExport()}>
            <Icon type="printer" />
            In
          </Btn>
          <BtnExport disabled={!this.enablePrintAndExport()} />
        </Row>
        <Row>
          <TableStudentData dataSource={this.state.students} rowSelection={studentSelection} />
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReTuitionReceipts {...props} />
  </Page>
);



