import React, { Component } from 'react';
import { Row, Col, Form, Radio, InputNumber } from 'antd';
import { moneyToString, stringToMoney } from 'utils/formatUtil';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;

    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Hc k 1',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDotthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getKhoanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khon thu 1',
        },
        {
          id: '2',
          label: 'Khon thu 2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019',
        },
        {
          id: '2',
          label: '2019-2020',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lp A',
        },
        {
          id: '2',
          label: 'Lp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    const { onChangeMasterPrice, masterPrice } = this.props;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <FilterPathEducate form={this.props.form} classes />
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lp">
              {getFieldDecorator('lop', {})(
                <SelectAjax service={this.getLop} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Nm hc">
              {getFieldDecorator('namhoc', {})(
                <SelectAjax service={this.getNamhoc} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Hc k">
              {getFieldDecorator('hocki', {})(
                <SelectAjax service={this.getSemester} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Khon thu">
              {getFieldDecorator('khoanthu', {})(
                <SelectAjax service={this.getKhoanthu} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="t thu">
              {getFieldDecorator('dotthu', {})(
                <SelectAjax service={this.getDotthu} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Nhp n gi mn">
              <InputNumber
                defaultValue={masterPrice}
                formatter={value => moneyToString(value)}
                parser={value => stringToMoney(value)}
                onChange={onChangeMasterPrice}
              />
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <b style={{ color: 'red' }}>Lu : D liu hc li, thi li ly t phng o to</b>
          </Col>
          <Col span={9}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('loaidanhsach', {})(
                <Radio.Group setFieldsValue={setFieldsValue}>
                  <Radio value="dshoclai">Lp DS hc li</Radio>
                  <Radio value="dsthilai">Lp DS thi li</Radio>
                  <Radio value="dsthilaicaithien">Lp DS thi li ci thin</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={3}>
            Tng s: <b style={{ color: 'red' }}>3</b>
          </Col>
          <Col span={6} style={{ float: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" title="Tm kim" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



