import React, { Component } from 'react';
import { Table } from 'antd';
import { moneyToString } from 'utils/formatUtil';

export default class TableStudentData extends Component {
  state = {};

  columns = [
    {
      title: 'M SV',
      dataIndex: 'masinhvien',
      key: 'masinhvien',
    },
    {
      title: 'H v tn',
      dataIndex: 'hovaten',
      key: 'hovaten',
    },
    {
      title: 'Ngy sinh',
      dataIndex: 'ngaysinh',
      key: 'ngaysinh',
    },
    {
      title: 'Gii tnh',
      dataIndex: 'gioitinh',
      key: 'gioitinh',
    },
    {
      title: 'Tn lp',
      dataIndex: 'tenlop',
      key: 'tenlop',
    },
    {
      title: 'S tin phi np',
      dataIndex: 'sotienphainop',
      key: 'sotienphainop',
      render: text => moneyToString(text),
    },
    {
      title: '% MG',
      dataIndex: 'percentmg',
      key: 'percentmg',
    },
    {
      title: 'S tin min gim',
      dataIndex: 'sotienmiengiam',
      key: 'sotienmiengiam',
      render: text => moneyToString(text),
    },
    {
      title: 'S tin cn np',
      dataIndex: 'sotienconnop',
      key: 'sotienconnop',
      render: text => moneyToString(text),
    },
    {
      title: 'S tin  np',
      dataIndex: 'sotiendanop',
      key: 'sotiendanop',
      render: text => moneyToString(text),
    },
    {
      title: 'Ghi ch',
      dataIndex: 'ghichu',
      key: 'ghichu',
    },
    {
      title: 'Ngy cp nht',
      dataIndex: 'ngaycapnhat',
      key: 'ngaycapnhat',
    },
  ];

  render() {
    const { rowSelection, dataSource } = this.props;
    return (
      <Table
        dataSource={dataSource}
        rowSelection={rowSelection}
        columns={this.columns}
        rowKey="key"
      />
    );
  }
}



